.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  font-weight: inherit;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.subtitle {
  font-weight: inherit;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .subtitle {
    font-size: 60px;
    line-height: 60px;
  }
}

.about {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .about {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .about {
    padding: 0 110px;
  }
}

.about__title {
  margin-top: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .about__title {
    margin-bottom: 32px;
  }
}

.about__list {
  display: grid;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  counter-reset: i;
}
@media (min-width: 768px) {
  .about__list {
    grid-template-columns: repeat(2, 35%);
    gap: 36px 19%;
  }
}
@media (min-width: 1440px) {
  .about__list {
    grid-template-columns: repeat(4, 20.5%);
    gap: 36px 6%;
  }
}

.about__item {
  position: relative;
  display: grid;
  gap: 20px;
  grid-template-columns: 60px 1fr;
  align-items: center;
  font-family: "Arial", sans-serif;
  color: #444444;
}
@media (min-width: 768px) {
  .about__item {
    grid-template-columns: 1fr;
    grid-template-rows: 80px 1fr;
    gap: 33px;
    padding: 40px 0 57px 0;
    font-size: 16px;
    line-height: 24px;
  }
}
.about__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50%;
}
@media (min-width: 768px) {
  .about__item::before {
    width: 80px;
    height: 80px;
  }
}
@media (min-width: 768px) {
  .about__item::after {
    counter-increment: i;
    content: counter(i);
    position: absolute;
    top: 0;
    left: 116px;
    z-index: -1;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
  }
}
.about__item--healthy::before {
  background-image: url("../img/index/icon-leaf.svg");
}
.about__item--cook::before {
  background-image: url("../img/index/icon-tub.svg");
}
.about__item--eat::before {
  background-image: url("../img/index/icon-eat.svg");
}
.about__item--result::before {
  background-image: url("../img/index/icon-alarm.svg");
}

.additional-product {
  position: relative;
  padding: 13px 0 19px;
}
.additional-product::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
  .additional-product::after {
    content: none;
  }
}
@media (min-width: 1440px) {
  .additional-product::after {
    content: "";
  }
}
@media (min-width: 768px) {
  .additional-product {
    display: grid;
    grid-template-areas: "title description" "button button";
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    gap: 21px;
    padding: 26px 37px 27px;
    border: 1px solid #cdcdcd;
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .additional-product {
    grid-template-areas: "title description button";
    grid-template-columns: 34% 34% 27%;
    align-items: center;
    padding: 15px 0 17px;
    border: none;
  }
}

.additional-product__title {
  margin: 0;
  margin-bottom: 13px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}
@media (min-width: 768px) {
  .additional-product__title {
    grid-area: title;
    margin: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

.additional-product__description-wrapper {
  display: grid;
  grid-template-columns: 45% 45%;
  justify-content: space-between;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .additional-product__description-wrapper {
    grid-area: description;
    grid-template-columns: 70% 22%;
    margin: 0;
    padding-top: 4px;
  }
}
@media (min-width: 1440px) {
  .additional-product__description-wrapper {
    grid-template-columns: 73% 20%;
    align-items: center;
  }
}

.additional-product__description {
  margin: 0;
  font-family: "Arial", sans-serif;
  line-height: 14px;
  color: #444444;
}
@media (min-width: 768px) {
  .additional-product__description {
    justify-self: end;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .additional-product__description {
    justify-self: start;
  }
}

.additional-product__price {
  justify-self: end;
  margin: 0;
  font-family: "Arial", sans-serif;
  line-height: 14px;
  color: #444444;
}
@media (min-width: 768px) {
  .additional-product__price {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .additional-product__price {
    justify-self: start;
  }
}

@media (min-width: 768px) {
  .additional-product__button {
    grid-area: button;
  }
}

.additional-products {
  padding: 25px 20px 35px;
}
@media (min-width: 768px) {
  .additional-products {
    padding: 68px 30px 77px;
  }
}
@media (min-width: 1440px) {
  .additional-products {
    display: grid;
    grid-template-areas: "title title" "list gift";
    grid-template-columns: 73% 20%;
    justify-content: space-between;
    padding: 77px 110px 85px;
