}

.additional-products__title {
  display: inline-block;
  position: relative;
  margin: 0;
  margin-bottom: 35px;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .additional-products__title::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    width: 10000%;
    height: 2px;
    background-color: #68b738;
    transform: translateY(3px);
  }
}
@media (min-width: 1440px) {
  .additional-products__title::before {
    right: calc(100% + 35px);
  }
}
.additional-products__title::after {
  content: "";
  position: absolute;
  top: 50%;
  left: calc(100% + 22px);
  width: 10000%;
  height: 2px;
  background-color: #68b738;
  transform: translateY(3px);
}
@media (min-width: 768px) {
  .additional-products__title::after {
    left: 100%;
  }
}
@media (min-width: 1440px) {
  .additional-products__title::after {
    left: calc(100% + 35px);
  }
}
@media (min-width: 768px) {
  .additional-products__title {
    margin-bottom: 80px;
    padding: 0 35px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .additional-products__title {
    grid-area: title;
    justify-self: start;
    padding: 0;
  }
}

.additional-products__list {
  position: relative;
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  list-style-type: none;
}
.additional-products__list::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
