  }
}
@media (min-width: 768px) {
  .additional-products__gift {
    flex-direction: row;
    min-height: 200px;
    padding: 64px 126px 61px 79px;
    text-align: left;
    /* stylelint-disable */
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85), 0%, rgba(104, 183, 56, 0.85) 100%), image-set(url("../img/catalog/gift-bg-tablet@2x.webp") 2x, url("../img/catalog/gift-bg-tablet.webp") 1x, url("../img/catalog/gift-bg-tablet@2x.jpg") 2x, url("../img/catalog/gift-bg-tablet.jpg") 1x);
    /* stylelint-enable */
  }
}
@media (min-width: 768px) {
  .additional-products__gift {
    grid-area: gift;
    align-self: start;
    flex-direction: column;
    min-height: 289px;
    padding: 56px 43px 42px 40px;
    text-align: center;
    /* stylelint-disable */
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85), 0%, rgba(104, 183, 56, 0.85) 100%), image-set(url("../img/catalog/gift-bg-mobile@2x.webp") 2x, url("../img/catalog/gift-bg-mobile.webp") 1x, url("../img/catalog/gift-bg-mobile@2x.jpg") 2x, url("../img/catalog/gift-bg-mobile.jpg") 1x);
    /* stylelint-enable */
