.control {
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.control--disabled {
  pointer-events: none;
}
@media (min-width: 768px) {
  .control {
    gap: 20px;
  }
}

.control__mark {
  position: relative;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #cdcdcd;
}
.control__mark:hover {
  border: 1px solid #bababa;
}
@media (min-width: 768px) {
  .control__mark {
    width: 40px;
    height: 40px;
  }
}

.control__input:focus + .control__mark {
  border: 1px solid #bababa;
}

.control--radio .control__mark {
  border-radius: 50%;
}

.control .control__input:checked ~ .control__mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  height: 14px;
  transform: translateX(-50%) translateY(-50%);
}
@media (min-width: 768px) {
  .control .control__input:checked ~ .control__mark::before {
    height: 18px;
  }
}

.control--radio .control__input:checked ~ .control__mark::before {
  width: 14px;
  border-radius: 50%;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .control--radio .control__input:checked ~ .control__mark::before {
    width: 18px;
  }
}

.control--checkbox .control__input:checked ~ .control__mark::before {
  width: 18px;
  background-image: url("../img/form/check.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 768px) {
  .control--checkbox .control__input:checked ~ .control__mark::before {
    width: 24px;
  }
}

.control--disabled .control__mark {
  opacity: 0.3;
}

.control__label {
  font-size: 16px;
  color: #222222;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .control__label {
    font-size: 20px;
    line-height: 30px;
  }
}

.control--disabled .control__label {
  opacity: 0.3;
}

